import { FaArrowRight } from "react-icons/fa";

function parseResult(result) {
  // Split the result into lines
  const lines = result.trim().split("\n");
  if (lines.length < 2) return null;
  // First line: actor chain
  const actors = lines[0].split(" -> ");
  // Next lines: movie info
  const movies = lines.slice(1).map(line => {
    // e.g. Tom Hanks is in Apollo 13 with Kevin Bacon
    const match = line.match(/is in (.+) with/);
    return match ? match[1] : "?";
  });
  return { actors, movies };
}

export default function PathResult({ result }) {
  const parsed = parseResult(result);
  if (!parsed) return <div className="text-gray-400">No path found.</div>;
  const { actors, movies } = parsed;

  return (
    <div className="flex flex-col items-center">
      <div className="flex flex-row items-center flex-wrap justify-center gap-2">
        {actors.map((actor, idx) => (
          <>
            <div
              key={"actor-" + idx}
              className="bg-blue-700 text-white px-4 py-2 rounded shadow font-semibold text-lg text-center min-w-[120px]"
            >
              {actor}
            </div>
            {idx < actors.length - 1 && (
              <div key={"arrow-" + idx} className="flex flex-col items-center mx-2">
                <FaArrowRight className="text-2xl text-blue-400" />
                <span className="text-xs text-gray-300 mt-1 max-w-[100px] text-center">{movies[idx]}</span>
              </div>
            )}
          </>
        ))}
      </div>
    </div>
  );
} 