"use client";
import { useState } from "react";
import SearchForm from "../components/SearchForm";
import PathResult from "../components/PathResult";

export default function HomePage() {
  const [result, setResult] = useState("");
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);

  const handleSearch = async (actor1, actor2) => {
    setLoading(true);
    setError("");
    setResult("");
    try {
      const res = await fetch(
        `${process.env.NEXT_PUBLIC_API_URL}/api/shortest-path?actor1=${encodeURIComponent(actor1)}&actor2=${encodeURIComponent(actor2)}`
      );
      const data = await res.json();
      if (data.error) {
        setError(data.error);
      } else {
        setResult(data.result);
      }
    } catch (e) {
      setError("Failed to connect to backend API.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <main className="flex flex-col items-center justify-start min-h-screen bg-gradient-to-b from-gray-900 to-gray-800 p-8">
      <h1 className="text-4xl font-bold text-white mb-4 text-center">Actor Shortest Path Finder</h1>
      <p className="text-lg text-gray-300 mb-8 max-w-5xl text-center">
        Find the shortest connection between two actors based on the movies they've acted in together.
      </p>
      <SearchForm onSearch={handleSearch} />
      {loading && <div className="text-blue-400 mt-6">Loading...</div>}
      {error && <div className="text-red-400 mt-6 max-w-xl text-center">{error}</div>}
      {result && !error && (
        <div className="mt-30"><PathResult result={result} /></div>
      )}
    </main>
  );
}
