import { useState, useEffect, useRef } from "react";

export default function SearchForm({ onSearch }) {
  const [actor1, setActor1] = useState("");
  const [actor2, setActor2] = useState("");
  const [actors, setActors] = useState([]);
  const [suggestions1, setSuggestions1] = useState([]);
  const [suggestions2, setSuggestions2] = useState([]);
  const [showSuggestions1, setShowSuggestions1] = useState(false);
  const [showSuggestions2, setShowSuggestions2] = useState(false);
  const input1Ref = useRef(null);
  const input2Ref = useRef(null);

  useEffect(() => {
    fetch(`${process.env.NEXT_PUBLIC_API_URL}/api/actors`)
      .then(res => res.json())
      .then(data => setActors(data))
      .catch(() => setActors([]));
  }, []);

  const handleInput1 = (e) => {
    const value = e.target.value;
    setActor1(value);
    if (value.length > 0) {
      setSuggestions1(actors.filter(a => a.toLowerCase().includes(value.toLowerCase())).slice(0, 8));
      setShowSuggestions1(true);
    } else {
      setSuggestions1([]);
      setShowSuggestions1(false);
    }
  };

  const handleInput2 = (e) => {
    const value = e.target.value;
    setActor2(value);
    if (value.length > 0) {
      setSuggestions2(actors.filter(a => a.toLowerCase().includes(value.toLowerCase())).slice(0, 8));
      setShowSuggestions2(true);
    } else {
      setSuggestions2([]);
      setShowSuggestions2(false);
    }
  };

  const handleSuggestionClick1 = (name) => {
    setActor1(name);
    setSuggestions1([]);
    setShowSuggestions1(false);
    input1Ref.current.blur();
  };

  const handleSuggestionClick2 = (name) => {
    setActor2(name);
    setSuggestions2([]);
    setShowSuggestions2(false);
    input2Ref.current.blur();
  };

  return (
    <form
      onSubmit={e => {
        e.preventDefault();
        onSearch(actor1, actor2);
      }}
      className="flex flex-col gap-4 items-center w-full max-w-lg"
      autoComplete="off"
    >
      <div className="relative w-full">
        <input
          ref={input1Ref}
          className="w-full p-3 rounded bg-gray-800 text-white placeholder-gray-400 border border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
          value={actor1}
          onChange={handleInput1}
          onFocus={() => actor1 && setShowSuggestions1(true)}
          onBlur={() => setTimeout(() => setShowSuggestions1(false), 100)}
          placeholder="Enter the first actor's name..."
          required
        />
        {showSuggestions1 && suggestions1.length > 0 && (
          <ul className="absolute z-10 w-full bg-gray-900 border border-gray-700 rounded mt-1 max-h-48 overflow-y-auto">
            {suggestions1.map((name, idx) => (
              <li
                key={idx}
                className="px-4 py-2 hover:bg-blue-700 cursor-pointer text-white"
                onMouseDown={() => handleSuggestionClick1(name)}
              >
                {name}
              </li>
            ))}
          </ul>
        )}
      </div>
      <div className="relative w-full">
        <input
          ref={input2Ref}
          className="w-full p-3 rounded bg-gray-800 text-white placeholder-gray-400 border border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
          value={actor2}
          onChange={handleInput2}
          onFocus={() => actor2 && setShowSuggestions2(true)}
          onBlur={() => setTimeout(() => setShowSuggestions2(false), 100)}
          placeholder="Enter the second actor's name..."
          required
        />
        {showSuggestions2 && suggestions2.length > 0 && (
          <ul className="absolute z-10 w-full bg-gray-900 border border-gray-700 rounded mt-1 max-h-48 overflow-y-auto">
            {suggestions2.map((name, idx) => (
              <li
                key={idx}
                className="px-4 py-2 hover:bg-blue-700 cursor-pointer text-white"
                onMouseDown={() => handleSuggestionClick2(name)}
              >
                {name}
              </li>
            ))}
          </ul>
        )}
      </div>
      <button
        type="submit"
        className="w-full bg-blue-600 hover:bg-blue-700 text-white font-bold py-3 px-6 rounded transition-colors"
      >
        Find Shortest Path
      </button>
    </form>
  );
} 